@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  font-family: "Lucida Sans Unicode";
  background-color:rgba(64,180,249) ;
}
.game{
  width:100vw;
  height:100vh;
  display: flex;
    justify-content: center;
    align-items: center;
  flex-direction: column;
}
.game-board {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50vh;
  width: 80vw;
  padding: 1vh;
  background: rgba(255,105,0,0);
}

.row {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1vh;
  padding: 1vh;
}

.cell {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8vh;
  width: 8vh;
  background-color: rgb(255, 255, 255,0.5);
  border-radius: 1vh;
}

.color-2 {
  background-color: rgb(250, 65, 108,0.9);
}

.color-4 {
  background-color: #4196FA;
}

.color-8 {
  background-color: #4177FA;
}

.color-16 {
  background-color: #4157FA;
}

.color-32 {
  background-color: #4B41FA;
}

.color-64 {
  background-color: #6A41FA;
}

.color-128 {
  background-color: #8A41FA;
}

.color-256 {
  background-color: #AA41FA;
}

.color-512 {
  background-color: #9D7B9D;
}

.color-1024 {
  background-color: #F9833E;
}

.color-2048 {
  background-color: #F9C13E;
}

.number {
  color: #fff;
  font-size: 5vh;
  font-weight: bold;
}

.score {
  text-align: center;
  margin-bottom: 1vh;
  font-size: 2vh;
  font-weight: bold;
}
.scoreBoard{
  position: absolute;
  right: 0;
  top: 15px;
  display:flex;
  transition: right 2s;
}
.scoreBoard > div{ 
  padding:10px;
  margin:0 10px; 
  text-align: center;
  background: rgba( 255, 255, 255, 0.15 );  
  backdrop-filter: blur( 8.0px );
  -webkit-backdrop-filter: blur( 8.0px );
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}
@media only screen and (min-width: 600px) {
  .scoreBoard {
    right:20%;
  }
}


footer{
  position:fixed;
  bottom:0;
  left:0;
  width: 100vw;
  padding:15px;
  text-align: center;
  background: rgba( 255, 255, 255, 0.15 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 8.0px );
-webkit-backdrop-filter: blur( 8.0px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 ); 
}

.go-to-home{
  position:absolute;
  left:0;
  top:0;
  cursor:pointer;
  padding:20px;
}
.go-to-home a svg{  
  color: rgba( 255, 255, 255, 0.45 );  
}
.go-to-home a svg:hover{  
  color: rgba( 255, 255, 255, 0.65 );  
}

.game-controls{
  padding-bottom: 9vh;
}
.game-controls button{
  margin: 10px 5px;
  background: rgba( 255, 255, 255, 0.15 );  
  backdrop-filter: blur( 8.0px );
  -webkit-backdrop-filter: blur( 8.0px );
  padding:5px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  cursor:pointer;
}
.game-controls button:hover{
  background: rgba( 255, 255, 255, 0.45 );    
}

footer > div a{
  font-size :1em;
  text-decoration: underline !important;
}


.title{
  font-size:13vh !important;
}
* {
  margin: 0;
  padding: 0;
  box-shadow: border-box;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}
section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: rgb(2,0,36);
background:rgba(64,180,249);
}


.container {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(0.5vh);
          backdrop-filter: blur(0.5vh);
  border-radius: 1vh;
  box-shadow: 0 2vw 4vh rgba(0, 0, 0, 0.2);
}
.title {
  font-weight: bolder;
  color:rgba(255, 255, 255, 0.2);

}

.form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color:rgba(64,180,249) ;
  width: 100vw;
  height: 100vh;
}
.form h2 {
  font-size: 3vh;
  color: rgb(254, 254, 254, 0.8);
  letter-spacing: 0.2vw;
  margin-bottom: 1vh;
  margin-top: 0;
}
.form .inputBx {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1vh;
  width: 100vw;
}
.form .inputBx span p {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 20vh;
  height: 17vh;
  outline: none;
  border: none;
  border: 1vh solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.2);
  padding: 1vh 2vh;
  border-radius: 1vh;
  color: rgb(0, 0, 0, 0.5);
  font-family: "Poppins", sans-serif;
  font-size: 1.7vh;
  font-weight: bold;
  box-shadow: 0 3vh 3vh rgba(0, 0, 0, 0.05);
}

.form h1{
    color: rgba(0, 0, 0, 0.5);
    font-size: 4.5vh;
    font-weight: bold;
}
span{
  margin:1vh 1vw;
}
a{
  font-size: 7vh;
}
a:visited,a:link,a:active,a:focus{
  text-decoration: none;
  border-color:none;
  color: rgba(0, 0, 0, 0.5);
}
.how-to-modal{
    position:absolute;
    left:0;
    top:0;
    width:100vw;
    height:100vh;
    background:rgba(32, 32, 32, 0.301);
    display: flex;
    justify-content: center;
    align-items: center;
}
.Modal-main-box{
    color:white;
    position:absolute;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    overflow: visible;
    width:clamp(90vw,50vw,500px);
    height:clamp(90vh,50vh,500px);
    display: flex;
    flex-direction:column;
    align-items: center;
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 8.0px );
    -webkit-backdrop-filter: blur( 8.0px );
    border-radius: 10px;    
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    z-index:3;
}
.Modal-main-box > h2,.Modal-main-box > p{
    margin: 20px 0px;
    width:80%;
}
.Modal-main-box > h2{
    text-decoration: underline;
    padding-left: 10%;
}
#Modal-Close{
    width: 20px;
    height:20px;
    position: absolute;
    right:-10px;
    top:-10px;
    z-index: 1;
    background: #ffffff8a;
    text-align:center;
    line-height:20px;
    border-radius: 50%;
    cursor:pointer;
}
iframe{
    width:clamp(40%,80%,500px);
    aspect-ratio: (315/560);
}
